import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getListHappyBucketHandle } from 'src/redux/actions/happyBucket.action';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import HappyBucketView from './happyBucket.view';

const HappyBucketContainer = () => {
  const [listHappyBucket, setListHappyBucket] = useState({
    data: [],
    page: 1,
    pageSize: 9,
  });
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(
      getListHappyBucketHandle(
        listHappyBucket.page,
        listHappyBucket.pageSize,
        (listDeal) => {
          console.log('listDeal', listDeal);
          setListHappyBucket({
            data: listDeal.data,
            page: 1,
            pageSize: 9,
            total: listDeal?.metadata?.total,
          });
        }
      )
    );
  }, []);
  const listBanner = useSelector(getListBannerSelector);
  return (
    <HappyBucketView
      listHappyBucket={listHappyBucket}
      listBanner={listBanner}
      onItemClick={(item) => {
        window.open(item.deep_link_url, '_self');
      }}
      onSeeMoreClick={() => {
        dispatch(
          getListHappyBucketHandle(
            listHappyBucket.page + 1,
            listHappyBucket.pageSize,
            (listDeal) => {
              setListHappyBucket({
                data: listHappyBucket.data.concat(listDeal.data),
                page: listHappyBucket.page + 1,
                pageSize: 9,
                total: listDeal?.metaData?.total,
              });
            }
          )
        );
      }}
    />
  );
};

export default HappyBucketContainer;
